html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "myriadpro-regular";
  src: url("../fonts/myriadpro-regular.woff2") format("woff2"), url("../fonts/myriadpro-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "academy-engraved";
  src: url("../fonts/academy-engraved.woff2") format("woff2"), url("../fonts/academy-engraved.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  color: #777777;
  font-family: "myriadpro-regular", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.2;
  background: #ffffff;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 940px) {
  .container {
    padding: 0 10px;
  }
}

svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header-page__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 0;
  background-color: #dddddd;
}

.header-page__center {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-page__descr {
  width: 40%;
  font-family: "academy-engraved", serif;
  font-size: 18px;
  color: #555555;
  text-transform: uppercase;
  padding-top: 45px;
  padding-bottom: 50px;
}

.header-page__logo {
  position: absolute;
  width: 20%;
  display: block;
  top: -12px;
  left: 50%;
  transform: translate(-50%, 0%);
}

.header-page__social {
  width: 40%;
  margin-right: 1px;
  padding-top: 45px;
  padding-bottom: 50px;
}

.service-menu {
  display: flex;
}

.service-menu__item:last-child .service-menu__link::before {
  display: none;
}

.service-menu__link {
  position: relative;
  display: block;
  padding: 0 10px;
  line-height: 1.2;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
}

.service-menu__link::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 1px;
  height: 12px;
  background-color: #000000;
}

.social-list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.social-list__item {
  margin-right: 19px;
}

.social-list__item:last-child {
  margin-right: 0;
}

.social-list__btn {
  position: relative;
  display: block;
  width: 23px;
  height: 22px;
  background-position: 50%;
  background-repeat: no-repeat;
}

.social-list__btn--pin {
  background-image: url(../img/ico/pin.png);
}

.social-list__btn--fb {
  background-image: url(../img/ico/fb.png);
}

.social-list__btn--insta {
  background-image: url(../img/ico/insta.png);
}

.logo {
  display: block;
}
